.section-5 {
    padding: 23px 0 32px 0;
}
.news {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    flex-wrap: wrap;
    &__item {
        margin: 0 15px 15px 15px;
        &_img {
            width: 100%;
        }
        &_content {
            &-title {
                font-weight: bold;
                font-size: 18px;
                line-height: 150%;
                margin-top: 11px;
            }
            &-date {
                font-weight: 600;
                font-size: 14px;
                line-height: 150%;
                color: #828282;
                margin-top: 6px;
                img {
                    margin-right: 6px;
                    display: inline;
                }
            }
            &-desc {
                font-weight: 600;
                font-size: 14px;
                line-height: 150%;
                color: #4F4F4F;
                margin-top: 8px;
            }
            &-btn {
                
                font-weight: bold;
                font-size: 18px;
                line-height: 122%;
                display: flex;
                align-items: center;
                text-align: center;
                justify-content: center;
                text-decoration-line: underline;
                color: #FF9900;
                margin: 0 auto;
                margin-top: 12px;
                &:hover {
                    color: #FF9900;
                    outline: none;
                    text-decoration-line: underline;
                }
            }
        }
    }
}
@media screen and (max-width: 1199px) {
    .news {
        &__item {
            width: 300px;
            &_content {
                &-title {
                    font-size: 16px;
                }
                &-date {
                    font-size: 12px;
                    img {
                        width: 15px;
                        margin-right: 5px;
                    }
                }
                &-desc {
                    font-size: 12px;
                }
                &-btn {
                    font-size: 16px;
                }
            }
        }
    } 
}

@media screen and (max-width: 991px) {
    .news {
        margin-top: 20px;
        &__item {
         
            margin: 0 10px 15px 10px;
            &_content {
                &-title {
                    font-size: 14px;
                    margin-top: 6px;
                }
                &-date {
                    font-size: 10px;
                    margin-top: 6px;
                    img {
                        width: 12px;
                        margin-right: 5px;
                    }
                }
                &-desc {
                    margin-top: 6px;
                    font-size: 10px;
                }
                &-btn {
                    margin-top: 8px;
                    font-size: 14px;
                }
            }
        }
    } 
}
@media screen and (max-width: 767px) {
    .news {
        margin-top: 15px;
        &__item {
            margin: 10px;
        }
    }
    .slick-dots {
        margin-bottom: -5px;
    }
}
