.section-9 {
    background: url(../img/footer-bg.jpg) no-repeat center center/ cover;
    padding: 60px 0;
    h2 {
        color: #fff;
        text-shadow: 0px 4px 12px rgba(0, 0, 0, 0.25);
    }
    .networks {
        display: flex;
        justify-content: center;
        margin-top: 35px;
        a {
            color: #fff;
            font-weight: bold;
            font-size: 16px;
            text-align: center;
            text-transform: uppercase;
            display: block;
            background: #FF9900;
            box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
            border-radius: 47px;
            width: 165px;
            line-height: 40px;
            &:first-child {
                margin-right: 30px;
                img {
                    transform: translateX(-10px);
                }
            }
            img {
                transform: translateX(-8px);
            }
        }
    }
    @media screen and (max-width: 575px) {
        padding: 20px 0 35px 0;
        h2 {
            font-size: 14px;
        }
        .networks {
            flex-direction: column;
            align-items: center;
            margin-top: 25px;
            a {
                &:first-child {
                    margin-right: 0;
                    margin-bottom: 22px;
                }
            }
        }
    }
}