.section-8 {
    padding: 29px 0 49px 0;
    background: #F1F4F5;
    .cities {
        margin-top: 13px;
        display: flex;
        flex-wrap: nowrap;
        
        /* хром, сафари */
        &::-webkit-scrollbar { 
            width: 0; 
        }

        /* ie 10+ */
        -ms-overflow-style: none; 

        /* фф (свойство больше не работает, других способов тоже нет)*/
        overflow: -moz-scrollbars-none;
        &__item {
            background: #fff;
            width: 165px;
            min-width: 165px;
            height: 96px;
            text-align: center;
            padding: 17px 15px 0;
            box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.15);
            border-radius: 2px;
            margin-right: 30px;
            
            &-title {
                font-weight: bold;
                font-size: 14px;
                line-height: 150%;
            }
            &-line {
                height: 4px;
                width: 100%;
                background: #FF9900;
                mix-blend-mode: normal;
                margin-top: 8px;
            }
            &-quantity {
                font-weight: 600;
                font-size: 12px;
                line-height: 150%;
                color: #4F4F4F;
                margin-top: 13px;
            }
        }
    }

    .quantity {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
  
        &__item {
            width: 33%;
            font-weight: bold;
            font-size: 16px;
            line-height: 20px;
            color: #284084;
            margin-top: 13px;
            position: relative;
            &:before {
                content: '';
                display: inline-block;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background: #FF9900;
                position: relative;
                top: -2px;
            }
            span {
                color: #4F4F4F;
            }
        }
        
    }
    .quantity__more {
        display: none;
    }


    @media screen and (max-width: 1199px) {
        .cities {
            overflow-y: visible;
        overflow-x: auto;
        padding: 15px;
        }
        .cities__item {
            margin-right: 25px;
        }
        .quantity {
            margin-top: -10px;
        }
    }
    @media screen and (max-width: 991px) {
        .cities__item {
            margin-right: 20px;
        }
        .quantity {
            &__item {
                width: 50%;
            }
        }
    }
    @media screen and (max-width: 767px) {
        padding-bottom: 30px;
        .quantity {
            margin-top: -20px;
            &__item {
                width: 100%;
                &:nth-child(n+4) {
                    display: none;
                }
                
            }
            &.active {
                .quantity__item {
                    display: block;
                }
                
            }
            
        }
        .cities {
            margin-top: 15px;
        }
        .quantity__more {
            font-weight: bold;
            font-size: 16px;
            line-height: 19px;
            display: flex;
            align-items: center;
            text-align: center;
            text-decoration-line: underline;
            color: #FF9900;
            margin: 0 auto;
            margin-top: 20px;
            &-l {
                display: none;
            }
            &.active {
                .quantity__more-m {
                    display: none;
                }
                .quantity__more-l {
                    display: inline;
                }
            }
        }
    }
    @media screen and (max-width: 575px) {
        padding: 27px 0;
        .cities__item {
            margin-right: 15px;
        }
        .cities {
            margin-top: 5px;
        }
        .quantity__item {
            margin-top: 18px;
        }
        .quantity__more {
            margin-top: 14px;
        }
    }
}