.section-6 {
    background: #F1F4F5;
    padding: 35px 0;
    .reviews {
        margin-top: 15px;
        &__item {
            background: #fff;
            padding: 35px 24px 35px;
            position: relative;
            margin: 25px 15px;
            box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.15);
            &:before {
                content: url(../img/left_quote.svg);
                display: block;
                position: absolute;
                left: 24px;
                top: -16px;
            }
            &:after {
                content: url(../img/right_quote.svg);
                display: block;
                position: absolute;
                right: 24px;
                bottom: -29px;
            }
            .news__item_content {
                &-title {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-top: 0;
                }
                &-smallTitle {
                    font-weight: bold;
                    font-size: 14px;
                    line-height: 150%;
                    text-align: center;
                    span {
                        position: relative;
                        &:after {
                            content: '';
                            display: block;
                            width: 76%;
                            height: 4px;
                            background: #FF9900;
                            position: absolute;
                            left: 12%;
                            bottom: -14px;
                        }
                    }
                }
                &-desc {
                    margin-top: 30px;
                }
                &-name {
                    font-weight: bold;
                    font-size: 18px;
                    line-height: 150%;
                    text-align: center;
                    color: #86296F;
                    margin-top: 14px;
                }
            }
        }
    }
}

@media screen and (max-width: 991px) {
    .section-6 {
        .reviews {
            &__item {
                margin: 25px 10px;
                padding: 35px 15px 20px 15px;
                &:before {
                }
                &:after {
                }
                .news__item_content {

                    &-title {
                    }
                    &-smallTitle {
                        font-size: 12px;
                        span {
                            &:after {
                                bottom: -12px;
                            }
                        }
                    }
                    &-desc {
                        margin-top: 25px;
                    }
                    &-name {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .section-6 {
        padding: 28px 0;
    }
    .section-6 .reviews {
        margin-top: 5px;
    }
}
@media screen and (max-width: 450px) {
    .section-6 {
        padding-bottom: 15px;
        .reviews {
            .slick-dots {
                transform: translateY(-7px);
            }
        }
    }
}