.section-3 {
    padding: 32px 0 56px 0;
    .celebrate {
        margin-top: -50px;
        &_item {
            display: flex;
            flex-direction: column;
         
            align-items: center;
            margin-top: 100px;
            img {
                height: 120px;
            }
            h3 {
                font-weight: bold;
                font-size: 24px;
                line-height: 29px;
                text-align: center;
                text-transform: uppercase;
                color: #393185;
                margin-top: 30px;
            }
        }
    }
    @media screen and (max-width: 1199px) {
        .celebrate {
         margin-top: -30px;
            &_item {
               margin-top: 70px;
                img {
                    height: 100px;
                }
                h3 {
                   font-size: 20px;
                   line-height: 25px;
                }
            }
        } 
    }
    @media screen and (max-width: 991px) {
        .celebrate {
         margin-top: -10px;
            &_item {
               margin-top: 60px;
                img {
                    height: 80px;
                }
                h3 {
                   font-size: 18px;
                   line-height: 22px;
                   margin-top: 20px;
                }
            }
        } 
    }
    @media screen and (max-width: 767px) {
        .celebrate {
         margin-top: -10px;
            &_item {
               margin-top: 60px;
                img {
                    height: 60px;
                }
                h3 {
                   font-size: 14px;
                   line-height: 17px;
                   margin-top: 15px;
                }
            }
        } 
    }
    @media screen and (max-width: 575px) {
        .celebrate {
         margin-top: -10px;
            &_item {
               margin-top: 40px;
                img {
                    height: 50px;
                }
                h3 {
                   font-size: 11px;
                   line-height: 15px;
                   margin-top: 10px;
                }
            }
        } 
    }
    @media screen and (max-width: 380px) {
        .celebrate {
         margin-top: -10px;
            &_item {
               margin-top: 35px;
                img {
                    height: 44px;
                }
                h3 {
                   font-size: 10px;
                   line-height: 12px;
                   margin-top: 13px;
                }
            }
        } 
    }
}