.section-7 {
    .balloon {
        display: flex;
        align-items: center;
        &__text {
            font-weight: 500;
            font-size: 18px;
            line-height: 150%;
            color: #4F4F4F;
            padding-right: 56px;
            p {
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
        img {
            display: block;
        }
    }
    @media screen and (max-width: 1199px) {
        .balloon{
            .balloon__text {
                font-size: 16px;
                padding-right: 25px;
            }
            img {
                width: 360px;
            }
        }
        
    }
    @media screen and (max-width: 991px) {
        .balloon{
            .balloon__text {
                font-size: 14px;
                padding-right: 10px;
            }
            img {
                width: 340px;
            }
        }
        
    }
    @media screen and (max-width: 767px) {
        .balloon{
            max-width: 100%;
            overflow: hidden;
            flex-direction: column;
            padding-top: 30px;
           
            .balloon__text {
              padding-right: 0;
            }
            img {
                width: 320px;
                margin-right: -20px;
            }
        }
    }
    @media screen and (max-width: 575px) {
        .balloon{
            padding-top: 23px;
            .balloon__text {
              padding-right: 0;
              font-size: 12px;
            }
            img {
                width: 290px;
               
            }
        }
    }
}