.section-4 {
    background: #F1F4F5;
    padding: 30px 0 54px 0;
    .top {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        &__item {
            width: 210px;
            box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
            margin-top: 20px;
            img {
                width: 100%;
                object-fit: cover;
            }
            &_text {
                padding: 11px 15px;
                &-title {
                    font-weight: 800;
                    font-size: 14px;
                    line-height: 150%;
                    text-transform: uppercase;
                    color: #393185;
                }
                &-desc  {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 12px;
                    line-height: 150%;
                    color: #4F4F4F;
                }
            }
        }
    }
    @media screen and (max-width: 1199px) {
        .top {
            &__item {
                width: 170px;
                img {
                }
                &_text {
                    &-title {
                        font-size: 13px;
                    }
                    &-desc  {
                        font-size: 11px;
                    }
                }
            }
        }
    }
    @media screen and (max-width: 991px) {
        .top {
            &__item {
                width: 125px;
                img {
                }
                &_text {
                    &-title {
                        font-size: 11px;
                    }
                    &-desc  {
                        font-size: 9px;
                    }
                }
            }
        }
    }
    @media screen and (max-width: 767px) {
        padding: 24px 0 44px 0;
        .top {
            margin-top: 10px;
            margin-bottom: 0;
            .slick-list {
                padding: 10px 5px;
            }
            &__item {
                margin: 0 10px;
                box-shadow: 0 3px 10px rgba(0,0,0,.15);
                img {
                }
                &_text {
                    &-title {
                    }
                    &-desc  {
                    }
                }
            }
            
        }
    }
    @media screen and (max-width: 575px) {
        .top__item_text-desc {
            display: none;
        }
        .top__item_text-title {
            font-size: 11px;
        }
        .top__item_text {
            padding: 10px;
        }
    }
    @media screen and (max-width: 420px) {
        .top__item_text-title {
            font-size: 9px;
        }
        .top__item_text {
            padding: 6px;
        }
        .top__item {
            margin: 0 5px;
            box-shadow: 0 2px 5px rgba(0,0,0,.15);
        }
    }

}
.slick-dots {
    li {
        button {
            &:before {
                content: '';
                width: 8px;
                height: 8px;
                background: #BDBDBD;
                border-radius: 50%;
                opacity: 1;
                top: 6px;
                left: 6px;
            }
        }
        &.slick-active {
            button {
                &:before {
                    width: 14px;
                    height: 14px;
                    background: #FF9900;
                    top: 3px;
                    left: 3px;
                }
            }
        }
    }
}