.section-2 {
    background: #f1f4f5;
    margin-top: 80px;
    padding-bottom: 63px;
    .search {
        margin: 30px auto 32px auto;
        width: 555px;
        border: 2px solid #FF9900;
        
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
        border-radius: 52px;
        display: flex;
        flex-wrap: nowrap;
        position: relative;
        height: 40px;
        max-width: 100%;
        input {
            width: 100%;
            max-width: 100%;
            border: none;
            border-radius: 52px;
            font-size: 16px;
            line-height: 36px;
            padding-left: 19px;
            padding-right: 40px;
            &::placeholder {
                color: #BDBDBD;
            }
        }
        &-btn {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background: #FF9900;
            position: absolute;
            top: -2px;
            right: -2px;
        }
    }
    .celebrating {
        width: 100%;
        background: #fff;
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
        padding: 30px 25px;
        height: 100%;
        h2 {
            text-transform: none;
            
           
            span {
                position: relative;
                width: auto;
                &:after {
                    content: '';
                    display: block;
                    width: 100%;
                    height: 4px;
                    position: absolute;
                    left: 0;
                    bottom: -9px;
                    background: #FF9900;
                }
            }
            
        }
        &__items {
            margin-top: 50px;
            li {
                list-style-type: none;
                margin-top: 30px;
                position: relative;
                &:after {
                    content: '';
                    display: block;
                    width: 0;
                    height: 0;
                    border-style: solid;
                    border-width: 5px 0 5px 5px;
                    border-color: transparent transparent transparent #ff9900;
                    position: absolute;
                    top: calc(50% - 5px);
                    right: 0;
                }
                &:first-child {
                    margin-top: 0;
                }
                a {
                    display: flex;
                    align-items: center;
                    font-weight: bold;
                    font-size: 16px;
                    line-height: 20px;
                    img {
                        height: 24px;
                        position: relative;
                        top: -2px;
                        margin-right: 13px;
                    }
                    
                }
                
                .celebrating__items-sub {
                   
                    position: absolute;
                    left: 235px;
                    top: 0;
                    background: #fff;
                    z-index: -1;
                    width: auto;
                    min-width: 100px;
                    padding: 0 25px 20px 20px;
                    opacity: 0;
                    transform: translateX(-100px);
                    transition: all 1s ease;
                    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.25);
                    li {
                        margin-top: 20px;
                        &:after {
                            right: -10px
                        }
                        &:first-child {
                            margin-top: none;
                        }
                    }
                    
                }
                &:hover {
                    .celebrating__items-sub {
                        z-index: 5;
                        transform: none;
                        opacity: 1;
                    }
                }
            }
        }
    }
    .carousel {
        height: 100%;
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.25);
        .carousel-inner {
            height: 100%;
        }
        .carousel-indicators {
            bottom: -45px;
            li {
                margin: 0 9px;
            }
        }
        &-item {
            display: flex;
            height: 100%;
            color: #fff;
            &__left {
                width: 360px;
                min-width: 360px;
                background: linear-gradient(62.54deg, #424C9F 11.46%, #811A82 88.56%);
                padding: 30px 32px 40px 32px;
                position: relative;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: space-between;
                h1 {
                    color: #fff;
                    font-weight: 800;
                    font-size: 28px;
                    line-height: 150%;
                    text-transform: uppercase;
                    position: relative;
                    margin-bottom: 40px;
                    &:after {
                        content: '';
                        display: block;
                        position: absolute;
                        width: 100%;
                        height: 8px;
                        bottom: -20px;
                        left: -3px;
                        background: #FF9900;
                    }
                }
                &-text {
                    margin-bottom: 20px;
                    font-weight: 600;
                    font-size: 18px;
                    line-height: 150%;

                   
                    
                    overflow: hidden;
                    
                        display: -webkit-box;
                    -webkit-line-clamp: 10; /* количество строк */
                    -webkit-box-orient: vertical;
                    text-overflow: ellipsis;
                }
                &-btn {
                    color: #fff;
                    width: 168px;
                    background: #FF9900;
                    border-radius: 47px;
                    line-height: 44px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-weight: bold;
                    font-size: 16px;
                    z-index: 3;
                    position: relative;
                    
                }
                    
                
            }
            &__right {
                flex-grow: 2;
                img {
                    object-fit: cover;
                    min-height: 100%;
                    min-width: 100%;
                }
            }
        }
    }
}



.carousel-fade .carousel-inner .item {
    opacity: 0;
    -webkit-transition-property: opacity;
    -moz-transition-property: opacity;
    -o-transition-property: opacity;
    transition-property: opacity;
  }
  .carousel-fade .carousel-inner .active {
    opacity: 1;
  }
  .carousel-fade .carousel-inner .active.left,
  .carousel-fade .carousel-inner .active.right {
    left: 0;
    opacity: 0;
    z-index: 1;
  }
  .carousel-fade .carousel-inner .next.left,
  .carousel-fade .carousel-inner .prev.right {
    opacity: 1;
  }
  .carousel-fade .carousel-control {
    z-index: 2;
  }
  .fade-carousel {
      position: relative;
      height: 100vh;
  }
  .fade-carousel .carousel-inner .item {
      height: 100vh;
  }
  .fade-carousel .carousel-indicators > li {
      margin: 0 2px;
      background-color: #BDBDBD;
      border-color: #BDBDBD;
      opacity: .7;
      width: 8px;
        height: 8px;
        border: none;
        border-radius: 50%;
  }
  .fade-carousel .carousel-indicators > li.active {
    width: 10px;
    height: 10px;
    opacity: 1;
    background-color: #FF9900;
    border-color: #FF9900;
    width: 14px;
    height: 14px;
    transform: translateY(-3px);
  }

/********************************/
.hero {
    
}


.fade-carousel .carousel-inner  .hero > * {
    opacity: 0;
    -webkit-transition: 2s all ease-in-out .1s;
       -moz-transition: 2s all ease-in-out .1s; 
        -ms-transition: 2s all ease-in-out .1s; 
         -o-transition: 2s all ease-in-out .1s; 
            transition: 2s all ease-in-out .1s; 
}
.fade-carousel .carousel-inner .active .hero > * {
    opacity: 1;
    -webkit-transition: 2s all ease-in-out .1s;
       -moz-transition: 2s all ease-in-out .1s; 
        -ms-transition: 2s all ease-in-out .1s; 
         -o-transition: 2s all ease-in-out .1s; 
            transition: 2s all ease-in-out .1s;    
}


@media screen and (max-width: 1199px) {
    .section-2 {
        margin-top: 70px;
    }
    .section-2 .celebrating {
        padding: 20px;
    }
    .section-2 .celebrating h2 {
        font-size: 20px;
    }
    .section-2 .celebrating__items {
        margin-top: 35px;
        li {
            margin-top: 25px;
            a {
                font-size: 15px;
                img {
                    height: 22px;
                }
            }
        }
    }
    .section-2 .carousel-item__left h1 {
        font-size: 26px;
        margin-bottom: 35px;
        &:after {
            height: 6px;
            bottom: -15px;
        }
    }
    .section-2 .carousel-item__left-text {
        font-size: 16px;
    }
}
@media screen and (max-width: 991px) {
    .section-2 .carousel-item__left {
        width: 60%;
        min-width: 60%;
        padding: 25px;
    }
    .section-2 .carousel-item__left h1 {
        font-size: 22px;
        margin-bottom: 25px;
        &:after {
            height: 5px;
            bottom: -10px;
        }
    }
    .section-2 .carousel-item__left-text {
        font-size: 14px;
    }
    .section-2 .carousel-item__left-btn {
        width: 130px;
        line-height: 35px;
        font-size: 14px;
    }
}
@media screen and (max-width: 767px) {
    .section-2 {
        margin-top: 60px;
    }
    .section-2 .carousel-item__left {
        width: 50%;
        min-width: 50%;
        padding: 20px;
        padding-top: 15px;
    }
    .section-2 .carousel-item__left h1 {
        font-size: 18px;
        margin-bottom: 20px;
        &:after {
            height: 4px;
            bottom: -7px;
        }
    }
    .section-2 .carousel-item__left-text {
        font-size: 13px;
        -webkit-line-clamp: 6; 
    }
    .section-2 .carousel-item__left-btn {
        width: 120px;
        line-height: 30px;
        font-size: 13px;
    }
}
@media screen and (max-width: 575px) {
    .section-2 {
     
    }
    .section-2 .carousel-item__left {
        padding: 15px;
        padding-top: 10px;
    }
    .section-2 .carousel-item__left h1 {
        text-align: start;
        font-size: 14px;
        margin-right: auto;
        margin-bottom: 15px;
        &:after {  
            left: 0;
        }
    }
    .section-2 .carousel-item__left-text {
       font-size: 12px;
       margin-bottom: 12px;

       -webkit-line-clamp: 5; /* количество строк */
    }
    .section-2 .carousel-item__left-btn {
        width: 110px;
        line-height: 28px;
        font-size: 12px;
    }
}
@media screen and (max-width: 375px) {
    .section-2 {
     
    }
    .section-2 .carousel-item__left {
        padding: 6px 15px 13px 12px;
        
    }
    .section-2 .carousel-item__left h1 {
        font-size: 12px;
        margin-bottom: 15px;
        &:after {  
            height: 3px;
            bottom: -5px;
        }
    }
    .section-2 .carousel-item__left-text {
       font-size: 10px;
       margin-bottom: 12px;
    }
    .section-2 .carousel-item__left-btn {
        width: 92px;
        line-height: 24px;
        font-size: 10px;
    }
}