.footer {
    position: relative;
    &:before {
        content: '';
        display: block;
        height: 2px;
        width: 100%;
        left: 0;
        bottom: 33px;
        position: absolute;
        background: #E0E0E0;
    }
    &-block {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px 0;
        &__menu {
            display: flex;
            margin-bottom: 0;
            
            li {
                list-style-type: none;
                margin-right: 53px;
                position: relative;
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 5px;
                    height: 5px;
                    background: #FF9900;
                    border-radius: 50%;
                    top: 6px;
                    left: -13px;
                }
                a {
                    font-weight: bold;
                    font-size: 16px;
                    line-height: 20px;
                    color: #284084;
                }
                &:last-child {
                    margin-right: 0;
                }
            }
        }
        .phones {
            display: flex;
            flex-direction: column;
            a {
                font-weight: bold;
                font-size: 14px;
                line-height: 17px;
                text-align: center;
                position: relative;
                img {
                    position: absolute;
                    left: -20px;
                }
                &:last-child {
                    margin-top: 15px;
                    img {
                        left: -17px;
                    }
                }
            }
        }
    }

    &__down {
        padding: 4px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        &-text {
            font-weight: bold;
            font-size: 12px;
            line-height: 15px;
            text-transform: uppercase;
            color: #828282;
        }
        &-links {
            display: flex;
            a {
                &:nth-child(1) {
                    margin-right: 19px;
                }
                img {
                    width: 24px;
                }
            }
        }
    }
}
@media screen and (max-width: 991px) {
    .footer {
        &-block {
            .logo {
                width: 70px;
                margin-right: 15px;
            }
            &__menu {
                li {
                    margin-right: 35px;
                    &:before {
                        top: 6px;
                        left: -10px;
                    }
                    a {
                        font-size: 13px;
                        line-height: 19px;

                    }
                }
            }
            .phones {
                a {
                    font-size: 13px;
                    img {
                        transform: scale(0.9);
                    }
                    &:last-child {
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 767px) {
    .footer {
        &-block {
            .logo {
                width: 60px;
                margin-right: 0px;
            }
            &__menu {
                li {
                    margin-right: 25px;
                    &:before {
                        top: 6px;
                        left: -10px;
                    }
                    a {
                        font-size: 12px;
                        line-height: 19px;

                    }
                }
            }
            .phones {
                a {
                    font-size: 12px;
                    img {
                        transform: scale(0.8);
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 575px) {
    .footer {
        &-block {
            padding: 14px 0;
            .logo {
                display: none;
            }
            &__menu {
                display: none;
            }
            .phones {
                width: 100%;
                flex-direction: row;
                justify-content: space-around;
                a {
                    display: block;
                    &:last-child {
                        margin-top: 0;
                    }
                    img {
                       position: static;
                       margin-right: 5px;

                    }
                }
            }
        }
    }
    .footer__down-text {
        font-size: 10px;
        line-height: 12px;
    }
}
@media screen and (max-width: 420px) {
    .footer {
        &-block {
            .phones {
                justify-content: space-between;
            }
        }
    }
}