
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
    &:focus {
        outline: none;
    }
    // border: 1px solid red;
    
    // &::-webkit-scrollbar {width: 0px;
    //     height: 0;
    // }
    
}

button {
    border: none;
    background: none;
    outline: none;
    &:focus {
        outline: none;
    }
}
a {
    text-decoration: none;
    text-transform: none;
    outline: none;
    color: #393185;
    &:focus {
        text-decoration: none;
        text-transform: none;
        outline: none;
    }
    &:active {
        text-decoration: none;
        text-transform: none;
        outline: none;
    }
    &:hover {
        text-decoration: none;
        text-transform: none;
        outline: none;
        color: #393185;
    }
}
ul {
    margin-bottom: 0;
}
body {
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    line-height: 150%;
    font-weight: 600;
    color: #393185;
    overflow-x: hidden;
    max-width: 100vw;
}
h2 {
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    span {
        position: relative;
        &:after {
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            height: 4px;
            bottom: -6px;
            left: 0px;
            background: #FF9900;
        }
    }
}

@media screen and (max-width: 991px) {
    h2 {
        font-size: 20px;
        line-height: 24px;
    }
}
@media screen and (max-width: 768px) {
    h2 {
        font-size: 18px;
        line-height: 22px;
    }
}
@media screen and (max-width: 575px) {
    h2 {
        font-size: 16px;
        line-height: 20px;
    }
}

