.header {
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
    background: #fff;
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100vw;
    &__block {
        display: flex;
        align-items: center;
        
        height: 80px;
        .header__block-mob {
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-grow: 2;
        }
        &-1 {
            display: flex;
            align-items: center;
            .logo {
                margin-right: 45px;
            }
            .location {
                display: flex;
                
                
                img {
                    margin-right: 8px;
                }
                a {
                    font-size: 18px;
                line-height: 22px;
                    position: relative;
                    align-items: center;
                font-weight: bold;
                    &:after {
                        content: '';
                        display: block;
                        position: absolute;
                        width: 0;
                        height: 0;
                        border-style: solid;
                        border-width: 5px 5px 0 5px;
                        border-color: #ff9900 transparent transparent transparent;
                        right: -13px;
                        top: 8px;
                    }
                }
            }
        }
        .menu {
            display: flex;
            margin-bottom: 0;
            margin-left: 100px;
            li {
                list-style-type: none;
                margin-right: 53px;
                position: relative;
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 5px;
                    height: 5px;
                    background: #FF9900;
                    border-radius: 50%;
                    top: 6px;
                    left: -13px;
                }
                a {
                    font-weight: 900;
                    font-size: 16px;
                    line-height: 20px;
                }
                &:last-child {
                    margin-right: 0;
                }
            }
        }
        &-3 {
            display: flex;
            align-items: center;
            .header__btn {
                margin-right: 23px;
                width: 165px;
                line-height: 40px;
                font-weight: bold;
                font-size: 16px;
                color: #fff;
                display: flex;
                background: #FF9900;
                border-radius: 47px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
            .lang {
                display: flex;
                font-weight: 600;
                
                a {
                    font-size: 14px;
                    line-height: 17px;
                }
                .active {
                    a {
                        font-weight: bold;
                    }
                    
                    position: relative;
                    &:after {
                        position: absolute;
                        content: '';
                        display: block;
                        background: #FF9900;
                        width: 100%;
                        height: 2px;
                        left: 0;
                        bottom: 0;
                    }
                }
                div {
                    &:first-child {
                        margin-right: 13px;
                    }
                }
                
            }
        }
    }
}

@media screen and (max-width: 1199px) {
    .header {
        &__block {
            height: 70px;
            &-1 {
                .logo {
                  width: 80px;
                }
                .location {
                    img {
                        width: 20px;
                    }
                    
                    a {
                        font-size: 16px;
                        line-height: 20px;
                    }
                }
            }
            .menu {
                margin-left: 75px;
                li {
                    margin-right: 40px;
                    a {
                        font-size: 14px;
                        line-height: 18px;
                    }
                }
            }
            &-3 {
                .header__btn {
                   width: 150px;
                   line-height: 35px;
                   font-size: 14px;
                }
                .lang {
                    a {
                        font-size: 13px;
                    }
                  
                    .active {
                    }        
                }
            }
        }
    }
}
@media screen and (max-width: 991px) {
    .header {
        &__block {
            &-1 {
                .logo {
                  width: 70px;
                  margin-right: 15px;
                }
                .location {
                    img {
                        width: 18px;
                    }
                    
                    a {
                        font-size: 15px;
                    line-height: 19px;
                        &:after {
                            border-width: 4px 4px 0 4px;
                            right: -10px;
                        }
                    }
                }
            }
            .menu {
                margin-left: 45px;
                li {
                    margin-right: 25px;
                    &:before {
                        top: 6px;
                        left: -10px;
                    }
                    a {
                        font-size: 12px;
                        line-height: 19px;

                    }
                }
            }
            &-3 {
                .header__btn {
                   width: 120px;
                   line-height: 28px;
                   font-size: 13px;
                   margin-right: 15px;
                }
                .lang {
                    a {
                        font-size: 13px;
                    }
                  
                   div {
                       &:first-child {
                           margin-right: 8px;
                       }
                   }       
                }
            }
        }
    }
}
@media screen and (max-width: 767px) {
    .header {
        z-index: 9999;
        &__block {
            height: 60px;
            
            
            &-1 {
                width: 100%;
                justify-content: space-between;
                .logo {
                    margin-right: 0;
                    width: 60px;
                }
                .location {
                    
                    img {
                        width: 18px;
                   
                    }
                    a {
                        font-size: 18px;
                        &:after {
                            right: -13px;
                            border-width: 5px 5px 0 5px;
                        }
                    }
                }
            }
            .menu {
               
                li {

                    &:before {

                    }
                    a {
                    }
                }
            }
            &-3 {
                position: relative;
                flex-direction: column;
                transform: translateY(-15vh);
                .header__btn {
                    width: 165px;
                    line-height: 40px;
                    font-size: 16px;
                    
                }
                .lang {
                    a {
                        font-size: 14px;
                        line-height: 17px;
                    }
                    
                    margin-top: 20px;
                   div {
                       &:first-child {
                     
                       }
                   }       
                }
            }

            .burger {
                width: 32px;
                height: 32px;
                padding: 5px 0;
                display: flex;
                justify-content: space-between;
                flex-direction: column;
                margin-right: 20px;
                div {
                    background: #FF9900;
                    width: 32px;
                    height: 3px;
                    border-radius: 2px;
                    transition: all 1s ease;
                }
            }
            .header__block-mob {
                opacity: 0;
                position: fixed;
                background: #fff;
                height: 100vh;
                width: 50vw;
                left: 0;
                top: 60px;
                flex-direction: column;
                padding-bottom: 80px;
                z-index: 99999999;
                box-shadow: inset 0px 7px 12px -7px rgba(0, 0, 0, 0.15);
                transform: translateX(-50vw);
                transition: all 1s ease;
                nav {
                    width: 100%;
                    padding-left: calc(25vw - 50px);
                }
                .menu {
                    flex-direction: column;
                    margin-left: 0;
                    margin-top: 35px;
                    height: 100%;
                    
                    li {
                        margin-right: 0;
                        margin-top: 32px;
                        &:nth-child(1) {
                            margin-top: 0;
                        }
                        a {
                            font-size: 16px;
                            line-height: 20px;
                        }
                    }
                }
            }

            
        }
    }

    
}

.header__block.active {
    .burger {
        div {
            &:nth-child(1) {
                transform: translate(0, 9.5px) rotate(45deg);
            }
            &:nth-child(2) {
                opacity: 0;
            }
            &:nth-child(3) {
                transform: translate(0, -9.5px) rotate(-45deg) ;
            }
        }
    }
    .header__block-mob {
        transform: translateX(0);
        opacity: 1;
    }
}
@media screen and (max-width: 575px) {
    .header__block .header__block-mob nav {
        padding-left: 28px;
    }
    .header__block {
        padding-right: 15px;
    }
    .header__block .header__block-mob {
        width: 70vw;
        transform: translateX(-70vw);
    }
    .header__block-1 .location {
        zoom: 110%;
    }
}
